h1#title{padding:2vh 0;font-size:3em;text-align:center}div#landing_pages,div#full_projects,div#side_projects,div#demos{margin:15px 0;border:3px solid #444;border-radius:10px}div#landing_pages button,div#full_projects button,div#side_projects button,div#demos button{width:100%;position:relative;border:none;border-radius:10px;padding:2vh 1.5vw;background-color:#000;color:#fff;font-size:2em;font-weight:bold;text-align:left;cursor:pointer}div#landing_pages button:hover,div#full_projects button:hover,div#side_projects button:hover,div#demos button:hover{background-color:#111}div#landing_pages button:after,div#full_projects button:after,div#side_projects button:after,div#demos button:after{content:"▼";position:absolute;left:96%}div#landing_pages div.list_content,div#full_projects div.list_content,div#side_projects div.list_content,div#demos div.list_content{max-height:0;overflow:hidden;background-color:#000;transition:max-height .5s ease-out}div#landing_pages div.list_content div.project,div#full_projects div.list_content div.project,div#side_projects div.list_content div.project,div#demos div.list_content div.project{display:flex;justify-content:space-between;margin:2vh 1vw;border:5px solid #222;border-radius:10px;padding:2vh 1vw}div#landing_pages div.list_content div.project div.description,div#full_projects div.list_content div.project div.description,div#side_projects div.list_content div.project div.description,div#demos div.list_content div.project div.description{width:55%;border-right:3px solid #444;padding-right:1vw;font-size:1.4em}div#landing_pages div.list_content div.project div.description h2,div#full_projects div.list_content div.project div.description h2,div#side_projects div.list_content div.project div.description h2,div#demos div.list_content div.project div.description h2{margin-top:10px;font-size:2em;text-align:center}div#landing_pages div.list_content div.project div.description p,div#full_projects div.list_content div.project div.description p,div#side_projects div.list_content div.project div.description p,div#demos div.list_content div.project div.description p{margin-top:20px;text-indent:1em}div#landing_pages div.list_content div.project div.video_container,div#full_projects div.list_content div.project div.video_container,div#side_projects div.list_content div.project div.video_container,div#demos div.list_content div.project div.video_container{width:44%;border:2px solid #666}div#landing_pages div.list_content div.project div.video_container video,div#full_projects div.list_content div.project div.video_container video,div#side_projects div.list_content div.project div.video_container video,div#demos div.list_content div.project div.video_container video{width:99.9%}div#landing_pages.active button:after,div#full_projects.active button:after,div#side_projects.active button:after,div#demos.active button:after{content:"▲"}div.link_to_project{display:flex;margin:2vh 1vw;border:5px solid #222;border-radius:10px;padding:2vh 1vw;text-decoration:none;background-color:#000;cursor:pointer}div.link_to_project div.text{margin-left:2%;font-size:1.2em}div.link_to_project div.text h3,div.link_to_project div.text p{margin-top:2vh}div.link_to_project div.text h3{font-size:1.6em}div.link_to_project div.text p{font-size:1.2em}div.link_to_project img{width:10%;height:15vh;object-fit:cover;object-position:50% 10%;border:2px solid #222;padding:5px}div.link_to_project:hover{background-color:#333}